import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './core/guards/auth.guard';

const routes: Routes = [
  { path: 'wygeneruj-rachunek', canActivate: [AuthGuard],
    loadChildren: () => import('./generator/generator.module').then(m => m.GeneratorModule)},
  { path: 'logowanie', loadChildren: () => import('./login/login.module').then(m => m.LoginModule)},
  {path: '**', redirectTo: '/wygeneruj-rachunek'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
