import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { ErrorData } from '../models/error-data.model';

@Injectable({
  providedIn: 'root'
})
export class ErrorService {
  private errorSubject = new BehaviorSubject<ErrorData>(null);

  constructor() { }

  get errorData(): Observable<ErrorData> {
    return this.errorSubject.asObservable();
  }

  setErrorData(errorData: ErrorData): void {
    this.errorSubject.next(errorData);
  }

}
