import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, take, tap } from 'rxjs/operators';
import { BehaviorSubject, Observable } from 'rxjs';
import { Router } from '@angular/router';
import { TokenDataService } from './token-data.service';
import { TokenData } from '../models/token-data.model';
import { environment } from '../../../environments/environment';
import { ErrorService } from './error.service';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  private jwtTokenSubject = new BehaviorSubject<TokenData>(null);
  private redirectTimeout: ReturnType<typeof setTimeout>;
  // moved it here because each call asObservable() returns new observable. In this case, all of the subscriptions share the same instance
  private jwtTokenObservable: Observable<TokenData> =  this.jwtTokenSubject.asObservable();

  constructor(
    private http: HttpClient,
    private router: Router,
    private tokenDataService: TokenDataService,
    private errorService: ErrorService) {
  }

  loginJwtToken(username: string, password: string): Observable<TokenData> {
    return this.http.post<any>(environment.url + '/login', {username, password})
      .pipe(
        take(1),
        tap(data => {
          this.tokenDataService.saveTokenData(data);
          this.jwtTokenSubject.next(data);
        })
      );
  }

  get jwtToken(): Observable<string> {
    return this.jwtTokenObservable.pipe(
      map((tokenData: TokenData) => {
        if (tokenData) {
          if (this.tokenDataService.validateToken(tokenData)) {
            return tokenData.token;
          } else if (!this.redirectTimeout) {
            this.tokenDataService.removeTokenData();
            this.jwtTokenSubject.next(null);
            this.redirectTimeout = setTimeout(() => {
              this.errorService.setErrorData(null);
              this.router.navigateByUrl('/logowanie');
              }, 3000);
          }
        }
        return null;
      })
    );
  }

  removeTokenData(): void {
    if (!this.redirectTimeout) {
      this.tokenDataService.removeTokenData();
      this.jwtTokenSubject.next(null);
      this.router.navigateByUrl('/logowanie');
    }
  }
}
