import { Component, OnInit } from '@angular/core';
import { AuthService } from './core/services/auth.service';
import { BehaviorSubject, combineLatest, Observable, Subject } from 'rxjs';
import { ErrorService } from './core/services/error.service';
import { ErrorData } from './core/models/error-data.model';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  loggedIn$: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);
  error$: BehaviorSubject<ErrorData> = new BehaviorSubject<ErrorData>(null);

  constructor(private readonly authService: AuthService,
              private readonly errorService: ErrorService) {
  }

  ngOnInit(): void {
    combineLatest([this.authService.jwtToken, this.errorService.errorData])
      .subscribe(([tokenData, errorData]) => {
        // console.log('e', errorData);
        this.loggedIn$.next(!!tokenData);
        this.error$.next(errorData);
      });
  }

  logout(): void {
    this.authService.removeTokenData();
    this.error$.next(null);
  }
}
